#Contact{
    background-color: #ffffff;
    min-height: 90vh;

    h2{
        color: #e7e7e7;
        font-size: 10vw;
        font-family: Handwriting;
        text-align: center;

        @media (max-width: 575.98px) {
            font-size: 24vw;
        }
    }

    h4{
        font-family: "SubTitles";
    }

    .contactText{
        position:relative;
        font-family: "Text";
        top: -10vh;
        left: 20vh;
        width: 140vh;
        font-size: 4vh;
        text-align: center;

        @media (max-width: 575.98px) {
            top: -8vh;
            left: 0;
            width: 100vh;
        }
    }

    p{
        position: relative;
        font-family: "Text";
        text-align: justify;
        @media (max-width: 575.98px) {
            top: 0;
        }
    }

    form{
        .form-label{
            font-size: 1vw;
            font-family: SubTitles;

            @media (max-width: 575.98px) {
                font-size: 4vw;
            }
        }

        button{
            font-family: "Handwriting";
            font-size: 2vh;
        }
    }
}