#Contact {
  background-color: #ffffff;
  min-height: 90vh;
}
#Contact h2 {
  color: #e7e7e7;
  font-size: 10vw;
  font-family: Handwriting;
  text-align: center;
}
@media (max-width: 575.98px) {
  #Contact h2 {
    font-size: 24vw;
  }
}
#Contact h4 {
  font-family: "SubTitles";
}
#Contact .contactText {
  position: relative;
  font-family: "Text";
  top: -10vh;
  left: 20vh;
  width: 140vh;
  font-size: 4vh;
  text-align: center;
}
@media (max-width: 575.98px) {
  #Contact .contactText {
    top: -8vh;
    left: 0;
    width: 100vh;
  }
}
#Contact p {
  position: relative;
  font-family: "Text";
  text-align: justify;
}
@media (max-width: 575.98px) {
  #Contact p {
    top: 0;
  }
}
#Contact form .form-label {
  font-size: 1vw;
  font-family: SubTitles;
}
@media (max-width: 575.98px) {
  #Contact form .form-label {
    font-size: 4vw;
  }
}
#Contact form button {
  font-family: "Handwriting";
  font-size: 2vh;
}/*# sourceMappingURL=Contact.css.map */